import React from 'react';
import PropTypes from 'prop-types';
import image from '../images/forte.png'
import { Link } from "gatsby";
import "./layout2.css";
//import Menu from "./menu.js";
// import { graphql } from "gatsby";

const Layout2 = ({children, data}) => (
    <div style={{
        margin: `0 auto`,
        maxWidth: 1170,
        padding: `0 1rem`,
    }}>
        <header style={{marginBottom: `1.5rem`}}>
        <Link to='/'> <img src={image} alt="Logo" /></Link>
        {/* <Menu /> */}
            <ul style={{
                listStyle: `none`,
                float: `right`,
            }}>
                <li style={{ display: `inline-block`, marginRight: `1rem`, }}><Link to='/'>Home</Link> </li>
                <li style={{ display: `inline-block`, marginRight: `1rem`, }}><Link to='/blog'>Blog</Link> </li>
                <li style={{ display: `inline-block`, marginRight: `1rem`, }}><Link to='/page-2'>Page 2</Link> </li>
                <li style={{ display: `inline-block`, marginRight: `1rem`, }}><Link to='/page-3'>Page 3</Link> </li>
                <li style={{ display: `inline-block`, marginRight: `1rem`, }}><Link to='/my-files'>Site Files</Link> </li>
                <li style={{ display: `inline-block`, marginRight: `1rem`, }}><Link to='/info/about'>About</Link> </li>
            </ul>
            {/* {data.allWordpressPage.edges.map(({ node }, index) => (
                <tr key={index}>
                    <td>{node.title}</td>
                    <td>{node.slug}</td>
                </tr>
            ))} */}
            <h3 style={{color:`orange`}}>Forte Site</h3>
        </header>
        {children}
    </div>
)

Layout2.propTypes = {
    children: PropTypes.node.isRequired,
}

export default Layout2;

